header.primary {
  position: relative;
}
.logo {
  position: relative;
  width: 140px;
  z-index: 100;
}
nav.primary {
  background-color: var(--color-bg-white);
  height: 100%;
  padding: 5vw;
  padding-top: 160px;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateX(0);
  transition: all 0.4s;
  width: 100%;
  z-index: 10;
}
.admin-bar nav.primary {
  height: calc(100% - 46px);
  top: 46px;
}
@media screen and (min-width: 540px) {
  .admin-bar nav.primary {
    height: auto;
  }
}

nav.primary > ul {
  display: flex;
  flex-direction: column;
}
nav.primary a {
  color: var(--color-black);
  display: block;
  font-family: var(--font-serif);
  margin-right: 14px;
  text-decoration: none;
  text-transform: uppercase;
}
#nav-toggle {
  border: none;
  display: block;
  font-family: var(--font-serif);
  font-size: 16px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100;
}
#nav-toggle:hover {
  cursor: pointer;
}
@media screen and (min-width: 541px) {
  .logo {
    width: 180px;
  }
  nav.primary {
    margin-top: 40px;
    padding: 0;
    position: static;
  }
  nav.primary.is-hidden {
    transform: none !important;
  }
  nav.primary > ul {
    flex-direction: row;
  }
  nav.primary {
    transform: none;
  }
  #nav-toggle {
    display: none;
  }
}
@media screen and (min-width: 1025px) {
  header.primary {
    align-self: start;
    grid-column: 1 / 2;
    position: sticky;
    top: 50px;
  }
  .logo {
    width: 144px;
  }
  nav.primary > ul {
    flex-direction: column;
  }
  nav.primary a {
    font-size: 16px;
  }
}
.archive-list h2:first-child {
  margin-top: 0;
}
.archive-list img {
  height: auto !important;
  width: 100% !important;
}
.post-type-archive main a {
  text-decoration: none;
}
@media screen and (min-width: 768px) {
  .grid-2-col img {
    grid-column: 1 / 2;
  }
  .grid-2-col > div {
    grid-column: 2 / 3;
  }
}
/* FLEX */
.flexible-content .flex-mod-img img {
  height: auto !important;
  width: 100% !important;
}
/* PERSON GALLERy*/
.flex-mod.flex-person-gallery {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(1, 1fr);
}
.flex-person > ul {
  font-family: "Crimson Text", serif;
  list-style-type: none;
  padding: 0;
  text-align: center;
}
.flex-person img {
  height: auto !important;
  width: 100% !important;
}
.person-name {
  font-size: 24px;
}
.person-phone {
  margin-top: 20px;
}
.person-phone, .person-email {
  font-size: 16px;
}
@media screen and (min-width: 640px) {
  .flex-mod.flex-person-gallery {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* SPLIDE IMAGE SLIDER */
.splide .splide__arrows {
  display: flex;
}
.splide .splide__arrow {
  background-color: transparent;
  left: auto;
  position: static;
  right: auto;
  top: 12px;
  transform: none;
  width: auto;
}
.splide .splide__arrow svg {
  fill: #a3a3a3;
  height: 10px;
  width: 10px;
}
.splide .splide__pagination {
  display: none;
}
.splide img {
  height: auto;
  width: 100%;
}
