* {
    box-sizing: border-box;
}
:root {
    --color-black: #333;
    --color-grey: #888;
    --color-bg-white: #fff;
    --font-serif: "Crimson Text", serif;
    --font-sans: "Open Sans", sans-serif;
}
html {
    margin: 0;
}
body {
    color: var(--color-black);
    font-family: var(--font-sans);
    font-weight: 300;
    line-height: 1.54;
    margin: 50px 0;
}
strong {
    font-weight: 400;
}
h1 {
    display: none;
    font-size: 20px;
    font-weight: 400;
    letter-spacing: 1px;
    margin-top: 0;
    text-align: right;
    text-transform: uppercase;
}
h2, h2 a {
    color: var(--color-black);
    font-size: 20px;
    font-weight: 700;
}
main {
    letter-spacing: 1px;
}
@media screen and (max-width: 1024px) {
    main {
        margin-top: 16px;
    }
}
@media screen and (min-width: 1025px) {
    h1 {
        display: block;
    }
}
h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-serif);
    font-weight: 400;
}
main {
    overflow: auto;
    width: 100%;
}
nav ul {
    list-style-type: none;
    margin: 0 ;
    padding: 0;
}
figcaption {
    color: var(--color-black);
    font-family: var(--font-serif);
    font-size: 10px;
}